export default class BrowserAction {
  static timer = null;

  static getPath(a) {
    return {
      path: {
        16: `assets/icons/16x16${a}`,
        24: `assets/icons/24x24${a}`,
        32: `assets/icons/32x32${a}`
      }
    };
  }

  static async setPopup(popup) {
    await chrome.action.setPopup({ popup });
  }

  static async setTitle(title) {
    await chrome.action.setTitle({ title });
  }

  static async setIcon(type) {
    await chrome.action.setIcon(this.getPath(type));
  }

  static async setUpdate() {
    await this.setPopup('');
    await this.setIcon('new.png');
  }

  static async setDefault() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }

    await this.setPopup('menu.html');
    await this.showBadge('');
    await this.setIcon('.png');
    await this.setTitle(chrome.i18n.getMessage('appName'));
  }

  static async setRec() {
    await this.setIcon('rec.png');
  }

  static async setPause() {
    await this.setIcon('paused.png');
  }

  static async setLoading() {
    const step = i => {
      this.setIcon(`progress${i ? '0' : ''}.png`);

      this.timer = setTimeout(step.bind(null, !i), 500);
    };

    step(true);
    await this.setTitle('Processing');
  }

  static async hidePopup() {
    await this.setPopup('');
  }

  static async showBadge(t) {
    await chrome.action.setBadgeText({ text: t.toString() });
    await chrome.action.setBadgeBackgroundColor({ color: '#000' });
  }
}
